  border-radius: 0 !important;
}

.capitalize {
  text-transform: capitalize !important;
}

.uppercase {
  text-transform: uppercase !important;
}

.dc-link {
  position: relative;
  padding-bottom: 4px;
  transition: color 0.25s ease;
  display: inline-block;
  text-transform: capitalize;

  &:after {
    content: "";
    background: #1b1d1d;
    height: 2px;
    width: 100%;
    bottom: 0;
    left: 0;
    position: absolute;
  }

  &:before {
    content: "";
    background: $hover;
    height: 2px;
    width: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    z-index: 1;
    transition: width 0.45s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  &:hover {
    color: $hover !important;

    &:before {
      width: 100%;
    }
  }

  &:focus {
    color: #1b1d1d;

    &:hover {
      color: $hover;
    }
  }
}

.bg {
  @extend %bg;
}

.dc-btn {
  font-size: 16px;
  padding: 10px 52px;
  background: transparent;
  border: 1px solid;
  position: relative;
  transition: all 0.3s ease;
  text-transform: capitalize;
  font-weight: 500;
  overflow: hidden;
  display: inline-block;

  span {
    z-index: 2;
    position: relative;
  }

  &:after {
    content: "";
    position: absolute;
    width: 0;
    height: 100%;
    left: 0;
    bottom: 0;
    top: 0;
    transition: width 0.6s cubic-bezier(0.85, 0, 0.15, 1);
    background: $hover;
  }

  &:hover {
    border-color: $hover !important;

    span {
      color: #fff !important;
      transition: all 0.3s ease;
    }

    &:after {
      width: 100%;
    }
  }
}

